import Bmob from "hydrogen-js-sdk";
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'
import uniPagination from "@/components/uni-pagination/uni-pagination.vue"
import common from '@/utils/common.js';

let uid;
let that;
let search_text = '';
let page_size = 30;
let page_num = 1;
export default {
	components: {
		loading,
		uniNavBar,
		faIcon,
		uniPagination,
		uniIcon
	},
	data() {
		return {
			page_num: 1,
			showOptions: false, //是否显示筛选
			loading: true,
			productList: null,
			checked_option: 'createdAt', //tab的筛选条件
			category: "", //选择的类别
			stock: "", //选择的仓库
			checked: false, //选择的是否失效
			stock_checked: false,
			search_text:''
		}
	},

	onLoad(option) {
		that = this;
		uni.removeStorageSync("category");
		uni.removeStorageSync("addMoreModel");

		uid = uni.getStorageSync('uid');
		
		if(option.search_text){
			that.search_text = option.search_text
			search_text = option.search_text
		}
	},
	onShow() {
		this.page_num = page_num
		uni.removeStorageSync("now_product");
		if (uni.getStorageSync("category")) {
			that.showOptions = true;
			that.category = uni.getStorageSync("category")
		}

		if (uni.getStorageSync("warehouse")) {
			that.stock = uni.getStorageSync("warehouse")[uni.getStorageSync("warehouse").length - 1].stock
			uni.setNavigationBarTitle({
				title:that.stock.stock_name+'的存货'
			})
		}

		that.get_productList();
	},

	onUnload() {
		common.handleData()
		//数据重置
		search_text = '';
		page_size = 50;
		
		uni.removeStorageSync("warehouse");
		uni.removeStorageSync("is_add");
	},

	methods: {

		//支持预览图片
		priviewImg(imgurl) {
			uni.previewImage({
				current:imgurl,
				urls: [imgurl],
			});
		},

		//分页点击
		change_page(e) {
			page_num = e.current
			that.get_productList();
		},

		//是否失效switch的改变
		change_timestatus(e) {
			console.log(e)
			that.checked = e.detail.value
		},

		//是否已预警的改变
		change_stocktatus(e) {
			console.log(e)
			that.stock_checked = e.detail.value
		},

		//输入框确定输入
		input_confirm(e) {
			console.log(e)
			search_text = e.detail.value
			that.get_productList();
		},

		//筛选点击
		shaixuan() {
			that.showOptions = true;
		},

		//确定点击
		goto_add() {
			uni.navigateTo({
				url: "../good_add/good_add"
			})
		},

		//modal重置的确认点击
		option_reset() {
			uni.removeStorageSync("category");
			uni.removeStorageSync("warehouse");
			that.stock_checked = false,
				that.checked = false,
				that.category = "";
			that.stock = "";
			that.showOptions = false;
			that.get_productList()
		},

		//modal筛选的确认点击
		option_confrim() {
			if (uni.getStorageSync("category")) {
				that.category = uni.getStorageSync("category")
			}

			if (uni.getStorageSync("warehouse")) {
				that.stock = uni.getStorageSync("warehouse")[uni.getStorageSync("warehouse").length - 1].stock
			}
			that.showOptions = false;
			that.get_productList()
		},

		//头部的options选择
		selectd(type) {
			page_size = 50;
			that.checked_option = type;
			that.get_productList();
		},

		//点击去到详情
		goDetail(value) {
			console.log(value)
			uni.setStorageSync("now_product", value);
			uni.navigateTo({
				url: "../good_det/good_det"
			})
		},

		//查询产品列表
		get_productList() {
			const query = Bmob.Query("NGoods");
			query.equalTo("userId", "==", uid);
			query.equalTo("stocks", "==", that.stock.objectId);
			query.equalTo("status", "!=", -1);
			if(that.stock){
				query.equalTo("order", "==", 1);
			}else{
				query.equalTo("order", "==", 0);
			}
			if(that.category.type == 1){
				query.equalTo("goodsClass", "==", that.category.objectId);
			}else{
				query.equalTo("second_class", "==", that.category.objectId);
			}
			const query1 = query.equalTo("goodsName", "==", {
				"$regex": "" + search_text + ".*"
			});
			const query2 = query.equalTo("packageContent", "==", {
				"$regex": "" + search_text + ".*"
			});
			query.or(query1, query2);

			if (that.checked) {
				var timestamp = Date.parse(new Date());
				query.equalTo("nousetime", "<=", timestamp);
			}
			if (that.stock_checked) {
				query.equalTo("stocktype", "==", 0);
			}
			query.limit(page_size);
			query.skip(page_size * (page_num - 1));
			query.order("-" + that.checked_option); //按照条件降序
			query.include("goodsClass", "stocks", "second_class");
			query.find().then(res => {
				//console.log(res)
				for(let item of res){
					item.reserve = item.reserve.toFixed(uni.getStorageSync("setting")?uni.getStorageSync("setting").show_float:0)
				}
				this.productList = res;
				this.loading = false;
			});
		},

	}
}
